let timer = '';
import {checkPhone,checkPwd,checkCode} from "@/common/common.js"
export default {
	data() {
		return {
			sendMsg:"发送验证码",
			isCanSendCode:true,
			isHidePassword:true,
			phone:"",
			password:"",
			code:""
		};
	},
	onLoad() {
	},
	methods: {
		isHidePasswordClick(){
			this.isHidePassword=!this.isHidePassword
		},
		setCodeInterval () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer);
		    timer = setInterval(() => {
		        time--;
		        this.isCanSendCode=false;
		        this.sendMsg=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode=true;
		            this.sendMsg="重新获取";
		            clearInterval(timer);
		        }
		    }, 1000)
		},
		clearCodeInterval(){
		    clearInterval(timer);
		    this.codemsg="获取验证码";
		},
		sendCode () {
			//发送验证码
		    if (!checkPhone(this.phone)) {
		        return
		    }
		    this.setCodeInterval();
			/*
			* 发送验证码逻辑
			*/
		},
		submit() {
			if(!checkPhone(this.phone)){
				return;
			};
			if(!checkPwd(this.password)){
				return;
			};
			if(!checkCode(this.code)){
				return;
			};
			/*
			* 修改密码逻辑
			*/
			this.$store.commit('update',['isLogin',true]);
			uni.reLaunch({
				url:'/pages/index/index'
			})
		}
	}
}
